export default {
	data () {
		return {
			top: (88 / 75 * 1 + 'rem'),
			arr: [],
			show: false,
			FieldType: '',
			MFItemId: '',
			deleteShow: false,
			DrId: this.$store.state.userInfo == null ? 18 : this.$store.state.userInfo.Data.User.DoctorId,
			h404: true,
			OrgIds: this.$store.state.userInfo == null ? 1 : this.$store.state.userInfo.Data.User.OrgId[0],
			start: this.$route.params.start,
			id: this.$route.query.id,
			title: '分组管理',
			manStart: false,
			MFItemArr: null,
		}
	},
	components:{
		
	},
	created(){
		this.httpStart();
		if(this.start == '1'){
			this.title = '分组管理'
			this.manStart = true
		}else{
			this.title = '选择分组'
			this.manStart = false
		}
	},
	methods: {
		esc () {
			this.show = false;
		},
		addPatientFn () {
			if(this.MFItemArr == null){
				alert('请选择分组')
			}else{
				let obj = {}
				if(this.id == undefined){
					return alert('患者不存在')
				}else{
					obj.parameter = {
						DrId: this.DrId,
						PatientId: this.id,
						MFItemId: this.MFItemArr.MFItemId,
						MFItemName: this.MFItemArr.MFItemName,
					}
					obj.url = '/PlatFormAPI/Memberfeatures/AddPatientFeatureRecord'
					this.$post(obj.url,obj.parameter).then(o => {
						if(o){
							this.$router.back()
						}
					})
				}
			}
		},
		circleFn(n,v){
			if(this.arr[n].checkedNames == 'circle'){
				this.arr.map((v,i) => {
					this.arr[i].checkedNames = 'circle'
				})
				this.MFItemArr = v
				this.arr[n].checkedNames = 'success'
			}else{
				this.arr.map((v,i) => {
					this.arr[i].checkedNames = 'circle'
				})
				this.MFItemArr = null
				this.arr[n].checkedNames = 'circle'
			}
		},
		httpStart () {
			let obj = {}
			obj.url = '/PlatFormAPI/Memberfeatures/QueryDrFeaturesItemForPage'
			obj.parameter = {
				"DrId": this.DrId,
				"OrgIds": this.OrgIds,
			}
			this.$post(obj.url,obj.parameter).then(o => {
				if(o.ReturnData != null){
					this.h404 = false
				}
				o = o.ReturnData
				if(o){
					o.map((v,n) => {
						o[n].checkedNames = 'circle'
					})
				}
				this.arr = o
			})
		},
		grouapsDetele (FieldType,MFItemId){ // 文档中删除
			this.show = true
			this.FieldType = FieldType
			this.MFItemId = MFItemId
			//let arr = []
			//this.arr.map((v,b) => { b != i ? arr.push(v) : ''})
			//this.arr = arr
		},
		grouapsDeteleDialog (){
			let obj = {}
			obj.url = '/PlatFormAPI/Memberfeatures/DelMemberfeaturesItem'
			obj.parameter = {
				DrId: this.DrId,
				FieldType: this.FieldType,
				MFItemId: this.MFItemId
			}
			this.$get(obj.url,obj.parameter).then(o => {
				if(o){
					this.show = false
					this.deleteShow = true
					this.httpStart ()
				}
			})
		}
	}
}



